lack, // (yellow)\n    white, // (red)\n    black  // (tan)\n) !default;\n\n.dayz {\n\n    @mixin triangle($direction){\n        $sizeH: $dayz-event-arrow-width;\n        $sizeV: $dayz-event-height / 2;\n        $color: $dayz-event-default-color;\n\n        content: '';\n        display: block;\n        width: 0;\n        height: 0;\n        border-style: solid;\n        position: absolute;\n        -moz-transform: scale(.9999);\n        @if $direction == left{\n            border-width: $sizeV $sizeH $sizeV 0;\n            border-color: transparent $color transparent transparent;\n            left: -1 * $dayz-event-arrow-width;\n            bottom: -1px;\n        }\n        @if $direction == right{\n            border-width: $sizeV 0 $sizeV $sizeH;\n            border-color: transparent transparent transparent $color;\n            right: -1 * $dayz-event-arrow-width;\n            top: -1px;\n        }\n    }\n\n    // these are internal\n    $hour-percent: 100% / 24;\n    $day-width:    100% / 7;\n    width:      100%;\n    height:     100%;\n    box-sizing: border-box;\n    display:    flex;\n    flex-wrap:  nowrap;\n    flex-direction: column;\n    align-items:    stretch;\n\n    .x-labels {\n        height: 30px;\n        align-items: center;\n        display: flex;\n        flex-direction: row;\n        .day-label {\n            display: flex;\n            flex-grow: 1;\n            flex-basis: 1px;\n            padding-left: 8px;\n        }\n    }\n\n    .body {\n        flex-grow: 1;\n        display: flex;\n        flex-direction: row;\n        position: relative;\n\n        .y-labels {\n            position: absolute;\n            width: 100%;\n            height: 100%;\n            width: $dayz-y-label-width;\n            display: flex;\n            flex-direction: column;\n            width: 100%;\n            .all-day, .hour {\n                width: 100%;\n                flex: 1;\n                display: flex;\n                justify-content: center;\n                flex-direction: column;\n                text-indent: 8px;\n                text-align: left;\n                border-bottom: 1px solid $dayz-border-color;\n            }\n            .all-day {\n                flex-grow: 0;\n            }\n        }\n\n        .days {\n            flex: 1;\n            display: flex;\n            flex-direction: row;\n            position: relative;\n            box-sizing: border-box;\n            border-top: 1px solid $dayz-border-color;\n            border-left: 1px solid $dayz-border-color;\n        }\n    }\n    .day {\n        border-right:  1px solid $dayz-border-color;\n        border-bottom: 1px solid $dayz-border-color;\n        box-sizing: border-box;\n\n        .event > * {\n            height: 100%;\n        }\n        &.highlight {\n            background-color: $dayz-highlight-color;\n        }\n    }\n\n    &.month, &.week .all-day, &.day .all-day {\n        .event {\n            $stack: 1;\n            @while $stack < 10 {\n                &.stack-#{$stack} { margin-top: ($stack * $dayz-event-height); }\n                $stack: $stack + 1;\n            }\n        }\n    }\n\n    &.week, &.day {\n        .x-labels{ padding-left: $dayz-y-label-width; }\n        .days {\n            margin-left: $dayz-y-label-width;\n        }\n        .day {\n            width: 100%;\n            display: flex;\n            flex-direction: column;\n            .all-day {\n                flex: 1;\n                flex-grow: 0;\n                .event {\n                    height: $dayz-event-height;\n                }\n            }\n            .events {\n                flex: 1;\n                position: relative;\n                .event.span-1 {\n                    width: 100%;\n                    position: absolute;\n                }\n            }\n            .label { display: none; }\n        }\n    }\n\n    &.week {\n        .events {\n            .day .event.span-1 {\n                width: $day-width;\n            }\n        }\n    }\n\n    &.month {\n        .days {\n            display: grid;\n            grid-auto-rows: 1fr;\n            grid-template-columns: repeat( 7, minmax($dayz-min-column-width, 1fr) );\n            .day {\n                .label {\n                    text-align: right;\n                    padding: 5px;\n                }\n                .event {\n                    height: $dayz-event-height;\n                    &.span-1 .evbody > div {\n                        text-overflow: ellipsis;\n                        white-space: nowrap;\n                        width: 100%;\n                        overflow: hidden;\n                    }\n                }\n                &.outside {\n                    background-color: $dayz-outside-background-color;\n                }\n            }\n        }\n    }\n\n    // styling in common for events in all displays\n    .event {\n        background: $dayz-event-default-color;\n        color: $dayz-event-text-default-color;\n        @each $current-color in $dayz-event-colors {\n            $i: index($dayz-event-colors, $current-color);\n            &.color-#{$i} .evbody {\n                background: $current-color;\n                color: nth($dayz-event-text-colors, $i);\n            }\n        }\n\n        box-sizing: border-box;\n        position: relative;\n        border: 1px solid $dayz-event-border-color;\n        border-radius: $dayz-event-border-radius;\n\n        $span: 0;\n        @while $span < 7 {\n            $span: $span + 1;\n            &.span-#{$span} {\n                z-index: 1;\n                width: calc(#{$span * 100%} + #{$span * 1px} - 1px);\n                &.is-continued {\n                    width: calc(#{$span * 100%} + #{($span * 1px - 2px)-$dayz-event-arrow-width});\n                    border-top-right-radius: 0;\n                    border-bottom-right-radius: 0;\n                }\n                &.is-continuation {\n                    width: calc(#{$span * 100%} + #{($span * 1px - 2px)-$dayz-event-arrow-width});\n                    border-top-left-radius: 0;\n                    border-bottom-left-radius: 0;\n                }\n                &.is-continued.is-continuation {\n                    width: calc(#{$span * 100%} + #{($span * 1px - 2px)-$dayz-event-arrow-width*2});\n                }\n            }\n        }\n\n\n        &.is-resizable {\n\n            &::before,\n            &::after {\n                content: \" \";\n                height: 3px;\n                border: 1px solid black;\n                right: 15%;\n                left: 15%;\n                display: block;\n                position: absolute;\n                border-radius: 5px;\n                visibility: hidden;\n                cursor: ns-resize;\n            }\n\n            @each $current-color in $dayz-event-colors {\n                $i: index($dayz-event-colors, $current-color);\n                &.color-#{$i} .evbody {\n                    &:before, &:after { background-color: darken($current-color, 15%); }\n                }\n            }\n\n            &:before {\n                top: -2px;\n            }\n            &:after {\n                bottom: -2px;\n            }\n            &.is-resizing, &:hover {\n                &:before, &:after {\n                    visibility: visible;\n                }\n            }\n        }\n\n        .evbody {\n            padding-left: 0.4rem;\n            height: 100%;\n            overflow: hidden;\n        }\n\n        &.is-continued {\n            border-top-right-radius: 0;\n            border-bottom-right-radius: 0;\n\n            &::after {\n                @include triangle(right);\n            }\n            @each $current-color in $dayz-event-colors {\n                $i: index($dayz-event-colors, $current-color);\n                &.color-#{$i} {\n                    &::after {\n                        border-color: transparent transparent transparent $current-color;\n                    }\n                }\n            }\n        }\n\n        &.is-continuation {\n            margin-left: $dayz-event-arrow-width;\n            border-top-left-radius: 0;\n            border-bottom-left-radius: 0;\n            &::before {\n                @include triangle(left);\n            }\n            @each $current-color in $dayz-event-colors {\n                $i: index($dayz-event-colors, $current-color);\n                &.color-#{$i} {\n                    &::before {\n                        border-color: transparent $current-color transparent transparent;\n                    }\n                }\n            }\n        }\n    }\n\n}\n"],"sourceRoot":""}]);

